/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

a, h1, h2, h3, h4, p {
  margin: 0;
}

.start {
  border: none;
  padding: 2rem 6rem;
  border-radius: 5rem;
  font-size: 2.2rem;
  font-weight: 700;
  color: #fff;
  background-color: hsl(180deg, 66%, 49%);
  margin: 3rem 0;
}
.start:hover {
  cursor: pointer;
  background-color: #51dbdb;
}

@media (min-width: 769px) {
  .container {
    width: 90%;
    margin: 0 auto;
  }
}

.no-display {
  display: none !important;
}

.alert {
  margin-top: 1rem;
  padding: 0;
  font-size: 1.4rem;
  color: hsl(0deg, 87%, 67%);
}

.red-border {
  border: 3.2px solid hsl(0deg, 87%, 67%) !important;
}

