.footer {
  display: block;
  text-align: center;
  margin: 0 auto;
  padding: 2rem;
  background-color: hsl(260deg, 8%, 14%);
  color: #fff;
}
.footer__logo {
  margin: 2rem;
}
.footer__logo path {
  fill: #fff;
}
@media (min-width: 769px) {
  .footer {
    display: flex;
    justify-content: space-between;
    padding: 4rem;
  }
}
.footer .resources,
.footer .features,
.footer .company {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.footer .resources h4,
.footer .features h4,
.footer .company h4 {
  margin-top: 4rem;
}
.footer .resources a,
.footer .features a,
.footer .company a {
  color: hsl(0deg, 0%, 75%);
}
@media (min-width: 769px) {
  .footer .resources,
.footer .features,
.footer .company {
    gap: 2rem;
  }
}
.footer .social-media {
  margin-top: 4rem;
}
.footer .social-media a {
  color: #fff;
  font-size: 2.4rem;
  margin-right: 2.2rem;
}
.footer .social-media a:last-of-type {
  margin-right: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  position: relative;
}
@media (min-width: 769px) {
  .header {
    margin: 2rem auto;
    display: grid;
    grid-template-columns: 12.1rem 2fr;
    gap: 3rem;
  }
}
.header #menuMobile {
  display: flex;
  flex-direction: column;
  position: absolute;
  margin-top: 27.5rem;
  padding: 2vh;
  width: 85vh;
  background-color: hsl(257deg, 27%, 26%);
  border-radius: 2rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}
.header #menuMobile a {
  font-size: 2.4rem;
  color: #fff;
  padding: 3rem;
  text-align: center;
}
.header #menuMobile .login {
  display: flex;
  flex-direction: column;
  border-top: 1px solid hsl(0deg, 0%, 75%);
}
.header #menuMobile .login a:last-of-type {
  background-color: hsl(180deg, 66%, 49%);
  border-radius: 5rem;
  margin-bottom: 2rem;
}

#burger-menu {
  display: inline-block;
  width: 3rem;
  position: relative;
  z-index: 2;
}
@media (min-width: 769px) {
  #burger-menu {
    display: none;
  }
}
#burger-menu .lines {
  display: block;
  margin-top: 0.7rem;
  min-width: 100%;
  height: 0.3rem;
  background-color: hsl(257deg, 7%, 63%);
}
#burger-menu .lines:first-of-type {
  margin-top: 0;
}

.desktop {
  display: none;
}
@media (min-width: 769px) {
  .desktop {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .desktop #menuDesktop {
    display: inherit;
    gap: 3rem;
  }
  .desktop #menuDesktop a {
    color: hsl(257deg, 7%, 63%);
    padding: 1rem;
  }
  .desktop .login {
    display: flex;
    gap: 3rem;
    align-items: center;
  }
  .desktop .login a {
    color: hsl(257deg, 7%, 63%);
    padding: 1rem;
  }
  .desktop .login a:last-of-type {
    padding: 1rem 2rem;
    background-color: hsl(180deg, 66%, 49%);
    border-radius: 5rem;
    color: #fff;
  }
  .desktop .login a:last-of-type:hover {
    cursor: pointer;
    background-color: #7ce4e4;
  }
}

.hero {
  padding: 2rem;
  overflow: hidden;
  text-align: center;
  position: relative;
}
.hero h1 {
  font-size: 4.8rem;
}
.hero p {
  color: hsl(257deg, 7%, 63%);
  font-weight: 500;
  line-height: 1.5;
}
@media (min-width: 769px) {
  .hero {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 2;
  }
  .hero__info {
    grid-column: 1/3;
    align-self: center;
    margin-right: 2rem;
    margin-left: 6rem;
  }
  .hero h1 {
    margin-top: 4rem;
    margin-bottom: 1rem;
    font-size: 6rem;
  }
  .hero h1, .hero p {
    text-align: left;
  }
  .hero img {
    grid-column: 3/5;
    grid-row: 1/3;
  }
  .hero button {
    padding: 1.5rem 1rem;
    margin: 0 2rem 0 6rem;
    align-self: flex-start;
    width: 25rem;
  }
}

.shortener {
  margin: -20.5rem 2rem 0 2rem;
  padding: 3.6rem;
  border-radius: 1rem;
  background-color: hsl(257deg, 27%, 26%);
  background-image: url(../../images/bg-shorten-mobile.svg);
  background-repeat: no-repeat;
  background-position: top right;
  background-size: 70%;
  position: relative;
  z-index: 2;
  top: 26.5rem;
}
@media (min-width: 769px) {
  .shortener {
    margin: 0rem 10rem 0 10rem;
    top: 7.5rem;
  }
}
.shortener form {
  display: flex;
  flex-direction: column;
}
.shortener form input {
  padding: 2rem;
  border-radius: 1rem;
  border: none;
}
.shortener form input[type=submit]:hover {
  cursor: pointer;
  background-color: #51dbdb;
}
.shortener form input::placeholder {
  color: hsl(0deg, 0%, 75%);
}
.shortener form input:last-of-type {
  background-color: hsl(180deg, 66%, 49%);
  color: #fff;
  font-size: 2.2rem;
  font-weight: 700;
  margin-top: 1.5rem;
}
@media (min-width: 769px) {
  .shortener form {
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }
  .shortener form input {
    padding: 2rem;
  }
  .shortener form input[type=text] {
    width: 90%;
  }
  .shortener form input[type=submit] {
    margin-top: 0;
  }
}

.statistics {
  position: relative;
  z-index: 0;
  background-color: #f2f2f2;
  padding: 2rem;
  text-align: center;
}
.statistics__header {
  padding: 2rem;
}
.statistics__header h2 {
  font-size: 3.6rem;
  margin-bottom: 2rem;
}
.statistics__header p {
  color: hsl(257deg, 7%, 63%);
  line-height: 1.5;
  padding-bottom: 4rem;
}
@media (min-width: 769px) {
  .statistics__header {
    padding: 2rem 24rem;
  }
}
.statistics-info::after {
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "";
  background: hsl(180deg, 66%, 49%);
  width: 1rem;
  height: 85rem;
}
@media (min-width: 769px) {
  .statistics-info::after {
    width: 85rem;
    height: 1rem;
    top: 55%;
  }
}
@media (min-width: 769px) {
  .statistics-info {
    display: flex;
    align-items: center;
    text-align: left;
    height: fit-content;
  }
}
.statistics__card {
  background-color: #fff;
  padding: 2rem;
  margin: 2rem 4rem 10rem 4rem;
}
.statistics__card h3 {
  font-size: 2.8rem;
  margin-bottom: 1rem;
}
.statistics__card p {
  color: hsl(257deg, 7%, 63%);
  line-height: 1.5;
}
@media (min-width: 769px) {
  .statistics__card {
    padding: 4rem;
  }
  .statistics__card h3, .statistics__card p {
    width: 90%;
    margin: 0 auto;
  }
  .statistics__card h3 {
    margin-bottom: 1rem;
  }
}
.statistics__card img {
  padding: 3rem;
  width: 12rem;
  background-color: hsl(257deg, 27%, 26%);
  border-radius: 50% 50%;
  position: relative;
  bottom: 5rem;
}
@media (min-width: 769px) {
  .statistics__card img {
    bottom: 5rem;
    margin-top: -3.6rem;
  }
}
.statistics__card:last-of-type {
  margin-bottom: 6rem;
}
@media (min-width: 769px) {
  .statistics__card:first-of-type {
    margin-bottom: 18rem;
  }
  .statistics__card:last-of-type {
    margin-top: 10rem;
  }
}

.boost {
  text-align: center;
  padding: 8rem;
  background-color: hsl(257deg, 27%, 26%);
  background-image: url(../../images/bg-boost-mobile.svg);
  background-repeat: no-repeat;
  background-position: center right;
  background-size: cover;
}
.boost h2 {
  color: #fff;
  margin-top: 2rem;
}

#results {
  padding: 12rem 2rem 2rem 2rem;
  margin-top: 16rem;
  background-color: #f2f2f2;
}
@media (min-width: 769px) {
  #results {
    margin-top: 0rem;
  }
}

.shorten-link {
  background-color: #fff;
  padding: 2rem;
  margin-bottom: 2rem;
  text-align: left;
  display: flex;
  flex-direction: column;
}
@media (min-width: 769px) {
  .shorten-link {
    margin: 0 8rem 2rem 8rem;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
  }
}
.shorten-link p {
  padding: 1rem;
  color: hsl(257deg, 27%, 26%);
  font-size: 1.8rem;
  font-weight: 700;
  border-bottom: 1px solid hsl(0deg, 0%, 75%);
}
@media (min-width: 769px) {
  .shorten-link p {
    border-bottom: none;
  }
}
.shorten-link a {
  padding: 2rem 1rem;
  font-size: 1.8rem;
  font-weight: 700;
  color: hsl(180deg, 66%, 49%);
}
.shorten-link a:hover {
  cursor: pointer;
}
.shorten-link button {
  padding: 1.4rem 2rem;
  border: none;
  border-radius: 1rem;
  color: #fff;
  background-color: hsl(180deg, 66%, 49%);
  font-size: 2.2rem;
  font-weight: 700;
}
@media (min-width: 769px) {
  .shorten-link button {
    padding: 1rem 2rem;
  }
}
.shorten-link button.copied {
  background-color: hsl(257deg, 27%, 26%);
}
.shorten-link button:hover {
  cursor: pointer;
  background-color: #7ce4e4;
}

.done-info {
  display: flex;
  flex-direction: column;
}
@media (min-width: 769px) {
  .done-info {
    flex-direction: row;
    gap: 1rem;
  }
}